<template>
  <div class="article-container">
   <el-card class="filter-card">
        <div slot="header" class="clearfix">
      <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
     <!-- / -->
        </div>
        <div class="text item">
   <!--  -->
   <el-form ref="form" :model="form" label-width="40px" size="mini">
    <el-form-item label="">
        <el-radio-group v-model="status">
        <el-radio :label="null"></el-radio>
        <el-radio :label="0"></el-radio>
        <el-radio :label="1"></el-radio>
        <el-radio :label="2"></el-radio>
        <el-radio :label="3"></el-radio>
        <el-radio :label="4"></el-radio>
        </el-radio-group>
    </el-form-item>
    <el-form-item label="">
        <el-select v-model="channelId" placeholder="">
        <el-option
              label=""
              :value="null"
            ></el-option>
        <el-option
        :label="channel.name"
        :value="channel.id"
        v-for="(channel, index) in channels"
        :key="index"
        ></el-option>
        </el-select>
    </el-form-item>
    <el-form-item label="">
        <el-date-picker
          v-model="rangeDate"
          type="datetimerange"
          start-placeholder=""
          end-placeholder=""
          :default-time="['12:00:00']"
          format="yyyy-MM-dd"
          value-format="yyyy-MM-dd"
          />
    </el-form-item>
    <el-form-item>
            <!--
            button  click 
            
           -->
        <el-button type="primary" :disabled="loading" @click="loadArticles(1)"></el-button>
    </el-form-item>
   </el-form>
   <!-- / -->
        </div>
   </el-card>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
        {{ totalCount }} 
        </div>
        <div class="text item">
   <!--  -->
   <!-- label , prop   -->
   <!--  table  data -->
     <el-table
      :data="articles"
      stripe
      size="mini"
      class="list-table"
      style="width: 100%"
      v-loading="loading"
      >
      <el-table-column
        prop="date"
        label="">
        <template slot-scope="scope">
          <el-image
              style="width: 100px; height: 100px"
              :src="scope.row.cover.images[0]"
              fit="cover"
              lazy
            ><div slot="placeholder" class="image-slot">
                <span class="dot">...</span>
              </div>
          </el-image>
          <!-- <img  v-if="scope.row.cover.images[0]"
          class="article-cover"
          :src="scope.row.cover.images[0]" alt="">
          <img v-else class="article-cover" src="./no-cover.gif" alt=""> -->
        </template>
      </el-table-column>
      <el-table-column
        prop="title"
        label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-tag type="articleStatus[scope.row.status].type">{{articleStatus[scope.row.status].text}}</el-tag>
          <!-- <el-tag v-if="scope.row.status === 1"></el-tag>
          <el-tag v-if="scope.row.status === 2" type="success"></el-tag>
          <el-tag v-if="scope.row.status === 3" type="danger"></el-tag>
          <el-tag v-if="scope.row.status === 4" type="info"></el-tag> -->
        </template>
      </el-table-column>
      <el-table-column
        prop="pubdate"
        label="">
      </el-table-column>
      <el-table-column label="">
        <!--  -->
        <template slot-scope="scope">
          <el-button
            size="mini"
            circle
            type="primary"
            icon="el-icon-edit"
            @click="$router.push('/publish?id=' + scope.row.id.toString())"></el-button>
          <el-button
            size="mini"
            circle
            type="danger"
            icon="el-icon-delete"
            @click="onDeleteArticle(scope.row.id)"></el-button>
      </template>
      </el-table-column>
    </el-table>
   <!--  -->
   <!--  -->
   <!-- total,10
   page-size  .sync  10 
   -->
    <el-pagination
      layout="prev, pager, next"
      background
      :total="totalCount"
      :disabled="loading"
      :current-page.sync="page"
      @current-change="onCurrentChange"
      :page-size="pageSize">
    </el-pagination>
   <!-- / -->
        </div>
   </el-card>

  </div>
</template>

<script>
// 
import { getArticles, getArticleChannels, deleteArticle } from '@/api/article'
export default {
  name: 'ArticleIndex',
  components: {},
  props: {},
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },

      articles: [], // 
      articleStatus: [
        { status: 0, text: '', type: 'info' }, // 0
        { status: 1, text: '', type: '' }, // 1
        { status: 2, text: '', type: 'success' }, // 2
        { status: 3, text: '', type: 'warning' }, // 3
        { status: 4, text: '', type: 'danger' } // 4
      ],
      totalCount: 0, // 0
      pageSize: 10, // 
      status: null, // 
      channels: [], // 
      channelId: null, // 
      rangeDate: null, // 
      loading: true, //  loading
      page: 1 // 
    }
  },
  computed: {},
  watch: {},
  created () {
    // 
    this.loadChannels()
    // ,1
    this.loadArticles(1)
  },
  mounted () {},
  methods: {
    onSubmit () {
      console.log('submit!')
    },
    // page = 1,,1,
    loadArticles (page) {
      getArticles({
        page,
        per_page: this.pageSize,
        status: this.status,
        channel_id: this.channelId,
        begin_pubdate: this.rangeDate ? this.rangeDate[0] : null, // 
        end_pubdate: this.rangeDate ? this.rangeDate[1] : null // 
      }).then(res => {
        // console.log(res)
        this.articles = res.data.data.results
        this.totalCount = res.data.data.total_count
        //  loading
        this.loading = false
      })
    },
    onCurrentChange (page) {
      this.loadArticles(page)
    },
    loadChannels () {
      getArticleChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    onDeleteArticle (articleId) {
      console.log(articleId)
      console.log(articleId.toString())
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        deleteArticle(articleId.toString()).then(res => {
          console.log(res)
          // 
          this.loadArticles(this.page)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
.filter-card {
    margin-bottom: 20px;
