<template>
  <div class="image-list">
      <div class="action-head">
      <el-radio-group
        v-model="collect"
        size="mini"
        @change="loadImages(1)"
      >
        <el-radio-button
          :label="false"
        ></el-radio-button>
        <el-radio-button
          :label="true"
        ></el-radio-button>
      </el-radio-group>
      <el-button
        v-if="isShowAdd"
        size="mini"
        type="success"
        @click="dialogUploadVisible = true"
      ></el-button>
    </div>
    <!--  -->
    <el-row :gutter="10">
      <el-col
        :xs="24"
        :sm="12"
        :md="8"
        :lg="6"
        v-for="(img, index) in images"
        :key="index"
        class="image-item"
        @click.native="selec(index)"
      >
        <el-image
          style="height: 100px"
          :src="img.url"
          fit="cover"
        ></el-image>
        <!--  -->
        <div v-if="isShowSelected && selected === index" class="selected"></div>
        <!-- / -->
        <div v-if="isShowAction" class="image-action">
          <!--
            class 
             {
                CSS: 
             }
             true
             false
           -->
          <el-button
            type="warning"
            :icon="img.is_collected ? 'el-icon-star-on' : 'el-icon-star-off'"
            circle
            size="small"
            @click="onCollect(img)"
            :loading="img.loading"
          ></el-button>
          <el-button
            size="small"
            type="danger"
            icon="el-icon-delete-solid"
            circle
            :loading="img.loading"
            @click="onDelete(img)"
          ></el-button>
          <!-- <i
            :class="{
              'el-icon-star-on': img.is_collected,
              'el-icon-star-off': !img.is_collected
            }"
            @click="onCollect(img)"
          ></i> -->
          <!-- <i class="el-icon-delete-solid"></i> -->
          <!-- <el-button size="mini" icon="el-icon-delete-solid" circle></el-button> -->
        </div>
      </el-col>
    </el-row>
    <!-- / -->

    <!--  -->
    <!--
      
      
     -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="totalCount"
      :page-size="pageSize"
      :current-page.sync="page"
      @current-change="onPageChange"
    >
    </el-pagination>
    <!-- / -->

    <el-dialog
      title=""
      :visible.sync="dialogUploadVisible"
      :append-to-body="true"
    >
      <!--
        upload 
         POST
        action
        headers
       -->
      <el-upload
        class="upload-demo"
        drag
        action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
        :headers="uploadHeaders"
        name="image"
        multiple
        :show-file-list="false"
        :on-success="onUploadSuccess"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
      </el-upload>
    </el-dialog>
  </div>
</template>

<script>
import {
  getImages,
  collectImage,
  deleteImage
} from '@/api/image'
export default {
  name: 'ImageList',
  components: {},
  props: {
    isShowAdd: {
      type: Boolean, // 
      default: true // 
    },
    isShowAction: {
      type: Boolean,
      default: true
    },
    isShowSelected: {
      type: Boolean,
      default: false
    }
  },
  data () {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      collect: false, // 
      images: [], // 
      dialogUploadVisible: false,
      uploadHeaders: {
        Authorization: `Bearer ${user.token}`
      },
      totalCount: 0, // 
      pageSize: 20, // 
      page: 1, // 
      selected: null, // 
      selectpage: 0, // 
      selectIndex: null // 
    }
  },
  computed: {},
  watch: {},
  created () {
    //  1 
    this.loadImages(1)
  },
  mounted () {},
  methods: {
    selec (id) {
      this.selected = id
      this.selectpage = this.page
      this.selectIndex = id
    },
    loadImages (page = 1) {
      // 
      this.page = page
      getImages({
        collect: this.collect,
        page,
        per_page: this.pageSize
      }).then(res => {
        const results = res.data.data.results
        results.forEach(img => {
          // img  loading 
          //  loading 
          img.loading = false
        })
        this.images = results
        this.totalCount = res.data.data.total_count
      })
    },

    // onCollectChange () {
    //   this.loadImages(1)
    // },

    onUploadSuccess () {
      // 
      this.dialogUploadVisible = false

      // 
      this.loadImages(this.page)

      this.$message({
        type: 'success',
        message: ''
      })
    },

    onPageChange (page) {
      if (page === this.selectpage) {
        this.selected = this.selectIndex
      } else {
        this.selected = null
      }
      this.loadImages(page)
    },

    onCollect (img) {
      //  loading
      img.loading = true
      collectImage(img.id, !img.is_collected).then(res => {
        // 
        img.is_collected = !img.is_collected
        //  loading
        img.loading = false
      })
      // if (img.is_collected) {
      //   // 
      //   collectImage(img.id, false)
      // } else {
      //   // 
      //   collectImage(img.id, true)
      // }
    },

    onDelete (img) {
      img.loading = true
      deleteImage(img.id).then(res => {
        // 
        this.loadImages(this.page)
        img.loading = false
      })
    }
  }
}
</script>

