<template>
  <div class="login-container">
       <!--
      el-form 
       el-form-item 
     -->
    <div class="login-form-wrap">
      <div class="login-head">
        <div class="logo"></div>
    </div>
     <!--
         Form 
        1 el-from  model 
        2 el-form-item  prop 
           prop 
        3 el-from  rules 
       -->

      <!--   
        1 el-form  ref 
        2 ref  el-form  validate 
       -->
    <el-form class="login-form" ref="login-form" :model="user" :rules="formRules">
      <el-form-item prop="mobile">
        <el-input placeholder="" v-model="user.mobile"></el-input>
      </el-form-item>
      <el-form-item prop="code">
        <el-input placeholder="" v-model="user.code"></el-input>
      </el-form-item>
       <el-form-item prop="agree">
        <el-checkbox v-model="user.agree"></el-checkbox>
      </el-form-item>
      <el-form-item>
        <el-button class="login-btn" type="primary"  :loading="loginLoding" @click="onLogin"></el-button>
      </el-form-item>
    </el-form>
  </div>
</div>
</template>

<script>
// userlogin
import { login } from '@/api/user'
export default {
  name: 'LoginIndex',
  data () {
    return {
      user: {
        mobile: '13911111111', // 
        code: '246810', // 
        agree: false // 
      },
      // checked: true, // 
      loginLoding: false, //  loading 
      formRules: { // 
      // []
        mobile: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^1[3|5|7|8|9]\d{9}$/, message: '', trigger: 'change' }
        ],
        code: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^\d{6}$/, message: '' }
        ],
        // 
        agree: [
          {
            // https://element.eleme.cn/#/zh-CN/component/form#zi-ding-yi-xiao-yan-gui-ze
            // callback()
            // callback(new Error(''))
            validator: (rule, value, callback) => {
              if (value) {
                callback()
              } else {
                callback(new Error(''))
              }
            },
            trigger: 'change'
          }
        ]
      }
    }
  },
  methods: {
    onLogin () {
    // 
      // const user = this.user
      // 
      // validate 
      this.$refs['login-form'].validate(valid => {
        // ,
        if (!valid) {
          return
        }
        // 
        this.login()
      })
    },
    login () {
      // 
      //  loading...
      this.loginLoding = true
      login(this.user).then(res => {
        console.log(res)
        // 
        this.$message({
          message: '',
          type: 'success'
        })
        // loading
        this.loginLoding = false
        // token,,user
        window.localStorage.setItem('user', JSON.stringify(res.data.data))
        // 
        this.$router.push({
          name: 'home'
        })
      }).catch(err => {
        console.log('', err)
        // 
        this.$message.error('')
        // loading
        this.loginLoding = false
      })
    }
  }
}
</script>

